import loadable from '@loadable/component'
import { Box, Flex, Heading, Text, Wrap } from '@chakra-ui/react'

const List = loadable(() => import('./List'))
const Map = loadable(() => import('./Map'))

export default ({ children = null, name, value,
   ...props }) => (
  <Flex direction="column" bg="#00FF0055" p={12} justify="center" align="center">
    {name && <Heading>{name}</Heading>}
    {((() => {
      if(Array.isArray(value)) {
        return <List {...{ value }} {...props}/>
      } else if(typeof value === 'string') {
        return <Text>{value}</Text>
      } else if(value instanceof Object) {
        return <Map {...{ value }} {...props}/>
      } else {
        return (
          <pre>
            {JSON.stringify(value)}
          </pre>
        )
      }
    })())}
    <Wrap>
      {children}
    </Wrap>
  </Flex>
)